.App-header {
    position: fixed;
    z-index: 1;

    background: #222 url("sky.jpg") center;
    background-size: cover;
    width: 750px;
    height: 500px;
    color: white;

    -webkit-transition: all 1s;

    -o-transition: all 1s;

    transition: all 1s;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    to {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotateZ(0deg);
                transform: rotateZ(0deg);
    }

    to {
        -webkit-transform: rotateZ(360deg);
                transform: rotateZ(360deg);
    }
}

.App-logo {
    position: absolute;
    top: 190px;
    left: calc(50% - 40px);

    width: 80px;
    height: 80px;

    -webkit-transition: all 1s;

    -o-transition: all 1s;

    transition: all 1s;

    /*animation: App-logo-spin 36s linear 0s infinite;*/
}

.App-search {
    position: absolute;
    top: 290px;
    right: calc(50% - 200px);

    width: 400px;
    height: 40px;

    display: inline-block;
    padding: 5px;
    border: solid 1px #ddd;
    border-radius: 5px;
    background-color: whitesmoke;

    -webkit-transition: all 1s;

    -o-transition: all 1s;

    transition: all 1s;
}

.App-search > *, .App-search:after {
    display: inline-block;
    vertical-align: middle;
}

.App-search:after {
    content: '';
    width: 0;
    height: 100%;
}

.App-search > img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
}

.App-search > input {
  color: black;
  outline: none;
  border-radius: 5px;
  padding: 0 5px;
  width: 345px;
  height: 30px;
  line-height: 25px;
}

.App-search.focused {
    -webkit-box-shadow: 0 0 5px blue;
            box-shadow: 0 0 5px blue;
}

.App-header.brief {
    top: 0;
    left: 0;
    height: 66px;
}

.App-header.brief > .App-logo {
    top: 0;
    left: 0;

    width: 66px;
    height: 66px;
}

.App-header.brief > .App-search {
    top: 13px;
    right: 10px;
}

.App-search > input[type=search] {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



