*, *:before, *:after {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin: 0;
    padding: 0;
    border: none;
    font-family: sans-serif;
}

.v-mid-box > *, .v-mid-box:after {
    display: inline-block;
    vertical-align: middle;
}

.v-mid-box:after {
    content: '';
    width: 0;
    height: 100%;
}



